<script>
    import {onMount} from 'svelte';
    import {firebase, db, googleProvider} from './../firebase.js';
    import {FirebaseApp, User, Doc, Collection} from 'sveltefire';
    import L from 'leaflet';

    const MY_ACCESS_TOKEN = 'pk.eyJ1IjoiZGFwcGRldnYiLCJhIjoiY2s3ZWo0dWV4MDYzcjNncHRncnlvdHY2OSJ9.zLk9G0tHK9iU-onN_TjxFw';
    
    // Map variables
    let map, LatLng, clickedMarkerId, currentZoom;

    // Layers
    let policeCarLayer, roadProblemLayer, taxiCarLayer;

    // Icons
    let placeholderIcon, policeCarIcon, dtpIcon, roadConstructionIcon;

    // Context menu
    let contextMenu, contextMenuDialog, menuDiv, dialogDiv;
    let contextMenuVisible = false;
    let contextMenuDialogVisible = false;

    onMount(mapInit)

    // AUTH USER and DB
    let getUser;
    let getUserAccount;

    firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
            // User is signed in.
            getUser = user;
            getMapInfo()
        } else {
            // No user is signed in.
            console.log('no user login')
        }
    });

    function mapInit() {

        // Map Layers
        policeCarLayer = L.layerGroup([]);
        roadProblemLayer = L.layerGroup([]);
        taxiCarLayer = L.layerGroup([]);

        var overlayMaps = {
            "Патрули ГИБДД, камеры": policeCarLayer,
            "Дорожные события": roadProblemLayer,
            "Такси": taxiCarLayer
        };

        // MAP
        map = L.map('map', {
            layers: [policeCarLayer, roadProblemLayer]
        }).setView([55.80046084146819, 49.105174541473396], 13);

        L.tileLayer('https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token={accessToken}', {
            attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery © <a href="https://www.mapbox.com/">Mapbox</a>',
            maxZoom: 18,
            id: 'mapbox/streets-v11',
            tileSize: 512,
            zoomOffset: -1,
            accessToken: MY_ACCESS_TOKEN
        }).addTo(map);

        // Layers control
        L.control.layers(null, overlayMaps).addTo(map);

        // Icons
        policeCarIcon = L.icon({
            iconUrl: './icons/police-car.png',
            iconSize:     [32, 32], // size of the icon
            iconAnchor:   [32, 32], // point of the icon which will correspond to marker's location
            popupAnchor:  [0, -32] // point from which the popup should open relative to the iconAnchor
        });

        placeholderIcon = L.icon({
            iconUrl: './icons/placeholder.png',
            iconSize:     [64, 64], // size of the icon
            iconAnchor:   [32, 32], // point of the icon which will correspond to marker's location
            popupAnchor:  [0, -32] // point from which the popup should open relative to the iconAnchor
        });

        roadConstructionIcon = L.icon({
            iconUrl: './icons/road-construction.png',
            iconSize:     [32, 32], // size of the icon
            iconAnchor:   [32, 32], // point of the icon which will correspond to marker's location
            popupAnchor:  [0, -32] // point from which the popup should open relative to the iconAnchor
        });

        dtpIcon = L.icon({
            iconUrl: './icons/car-crash.png',
            iconSize:     [32, 32], // size of the icon
            iconAnchor:   [32, 32], // point of the icon which will correspond to marker's location
            popupAnchor:  [0, -32] // point from which the popup should open relative to the iconAnchor
        });


        // Get user location
        map.locate({watch: false, setView: true, enableHighAccuracy: true});

        // Events
        map.on('locationfound', locationFound);
        map.on('contextmenu', contextMenuOpen);
        map.on('resize', mapResized);
        map.on('zoomend', mapZoomed);
    
        // Context menu
        let el = document.getElementById('context-menu');
        menuDiv = L.divIcon({html: el, className: 'context-menu'});

        // Context menu dialog
        let dialogEl = document.getElementById('context-menu-dialog');
        dialogDiv = L.divIcon({html: dialogEl, className: 'context-menu-dialog', iconAnchor: [75, -32]});
        
    }

    // Zoom event
    function mapZoomed(e) {
        currentZoom = e.target._zoom;
        console.log('target', e.target._zoom)

        if (currentZoom <= 12) {
            
            policeCarLayer.eachLayer(function (layer) {
                layer.options.icon.options.iconSize = [16, 16]
            });
            
        } else {
            policeCarLayer.eachLayer(function (layer) {
                layer.options.icon.options.iconSize = [32, 32]
            });
        }
    }

    // Resize event
    function mapResized(e) {
        console.log('map Resized')
    }

    // Set user marker
    function locationFound(e) {
        L.marker(e.latlng, {icon: placeholderIcon}).addTo(map).bindPopup("Ты здесь!");
    }

     // Open context menu
    function contextMenuOpen(e) {
            
        LatLng = e.latlng;
        contextMenuVisible = true;
        contextMenu = L.marker(e.latlng, {icon: menuDiv});
        contextMenu.addTo(map);
        contextMenu.once({mouseout: contextMenuClose});
    }

    // Close context menu
    function contextMenuClose(e) {
        e.target.remove();
    }

    // Add police car icon
    function addPoliceCar() {
        
        db.collection('icons/policeCar/onmap').add({
            added: firebase.firestore.FieldValue.serverTimestamp(),
            location: [LatLng.lat, LatLng.lng],
            author: getUser.uid,
            actual: 0.7
        })
        .then(function(docRef) {
            console.log("Document written with ID: ", docRef.id);
        })
        .catch(function(error) {
            console.error("Error adding document: ", error);
        });

        L.marker(LatLng, {icon: policeCarIcon}).addTo(map).bindPopup("Обновите страницу!");


        // if(getUserAccount.mapRating >= 5) {
        // }

        // else {
        //     let popup = L.popup()
        //     .setLatLng(LatLng)
        //     .setContent('<p>Ваш рейтинг ниже требуемого.</p>')
        //     .openOn(map);
        // }
        
    }

    // Add DTP icon
    function addDTPicon() {
        
        if(getUserAccount.mapRating >= 5) {
            db.collection('icons/DTP/onmap').add({
                added: firebase.firestore.FieldValue.serverTimestamp(),
                location: [LatLng.lat, LatLng.lng],
                author: getUser.uid,
                actual: 0.7
            })
            .then(function(docRef) {
                console.log("Document written with ID: ", docRef.id);
            })
            .catch(function(error) {
                console.error("Error adding document: ", error);
            });

            L.marker(LatLng, {icon: dtpIcon}).addTo(map).bindPopup("Обновите страницу!");
        }
        else {
            let popup = L.popup()
            .setLatLng(LatLng)
            .setContent('<p>Ваш рейтинг ниже требуемого.</p>')
            .openOn(map);
        }
        
    }

    // Add road construction icon
    function addRoadConstructionIcon() {
        
        if(getUserAccount.mapRating >= 5) {
            db.collection('icons/roadConstruction/onmap').add({
                added: firebase.firestore.FieldValue.serverTimestamp(),
                location: [LatLng.lat, LatLng.lng],
                author: getUser.uid,
                actual: 0.7
            })
            .then(function(docRef) {
                console.log("Document written with ID: ", docRef.id);
            })
            .catch(function(error) {
                console.error("Error adding document: ", error);
            });

            L.marker(LatLng, {icon: roadConstructionIcon}).addTo(map).bindPopup("Обновите страницу!");
        }
        else {
            let popup = L.popup()
            .setLatLng(LatLng)
            .setContent('<p>Ваш рейтинг ниже требуемого.</p>')
            .openOn(map);
        }
        
    }

    // Get MAP info
    function getMapInfo() {

        // Get user account info
        console.log('user id: ', getUser.uid)

        let userRef = db.collection('users');

        let accountInfo = userRef.where('uid', '==', getUser.uid);

        accountInfo.get().then(function(querySnapshot) {
            querySnapshot.forEach(function(doc) {
                
                getUserAccount = doc.data();
                console.log('mapRating', getUserAccount.mapRating);
            });
        });

        // Get police car collection
        let policeCarRef = db.collection('icons/policeCar/onmap');

        policeCarRef.get()
        .then(function(querySnapshot) {
            querySnapshot.forEach(function(doc) {
                
                let mark = doc.data();

                if(mark.actual >= 0.1) {
                let mDate = mark.added.toDate();
                let marker = L.marker(mark.location, {
                    icon: policeCarIcon,
                    opacity: mark.actual,
                    alt: doc.id
                    }
                ).bindPopup(`<p><b> Патруль ГИБДД </b></p><p> Добавлено ${mDate.getDate()}-го, в ${mDate.getHours()}:${mDate.getMinutes()} </p>`);

                marker.on('click', markerClicked);

                policeCarLayer.addLayer(marker);

                }

            });
        })
        .catch(function(error) {
            console.log("Error getting documents: ", error);
        });
    }

    // Marker event
    function markerClicked(e) {
        clickedMarkerId = e.target.options.alt
        contextMenuDialogVisible = true;
        contextMenuDialog = L.marker(e.latlng, {icon: dialogDiv}).addTo(map);
        contextMenuDialog.on('click', contextMenuDialogClose);
    }

    function contextMenuDialogClose(e) {
        e.target.remove();
    }

    function markerTrue() {
        
        let markerRef = db.collection('icons/policeCar/onmap').doc(`${clickedMarkerId}`);
        markerRef.update({
            actual: firebase.firestore.FieldValue.increment(0.11)
        })
        .then(function() {
            console.log("Document successfully updated!");
        })
        .catch(function(error) {
            // The document probably doesn't exist.
            console.error("Error updating document: ", error);
        });
    }

    function markerFalse() {

        let markerRef = db.collection('icons/policeCar/onmap').doc(`${clickedMarkerId}`);
        markerRef.update({
            actual: firebase.firestore.FieldValue.increment(-0.11)
        })
        .then(function() {
            console.log("Document successfully updated!");
        })
        .catch(function(error) {
            // The document probably doesn't exist.
            console.error("Error updating document: ", error);
        });
    }
    

</script>

<div class="pt-16">

    <div id="map" style="height: 700px; " class="z-0 w-full h-full">
    </div>

    <div class:contextMenuVisible id="context-menu" class="hidden context-menu">
        <p class="text-center font-semibold text-base text-red-600">Что здесь?</p>
        <div class="context-menu-item">
            <button class="context-menu-button" on:click={addPoliceCar}>Патрульная машина ГИБДД</button>
        </div>
        <div class="context-menu-item">
            <button class="context-menu-button" on:click={addRoadConstructionIcon}>Ремонт дороги</button>
        </div>
        <div class="context-menu-item">
            <button class="context-menu-button" on:click={addDTPicon}>ДТП</button>
        </div>
    </div>

    <div class:contextMenuDialogVisible id="context-menu-dialog" class="hidden context-menu-dialog">
        <div class="mt-2 ml-1">
            <button class="p-3 bg-green-600 rounded-lg text-white text-base" on:click={markerTrue}>Правда </button>
            <button class="p-3 ml-1 bg-red-600 rounded-lg text-white text-base" on:click={markerFalse}>Ложь</button>
        </div>
        <div class="mt-3 text-center">
            <button class="p-3 px-8 bg-gray-800 rounded-lg text-white text-base">Закрыть</button>
        </div>
    </div>
</div>

<style>.contextMenuVisible {
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
}

.contextMenuDialogVisible {
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
}

